













































































































/* margin padding reset */
body,
div,
p,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
img,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select {
  margin: 0;
  padding: 0;
}
/* remove list point mark */
ul,
ol {
  list-style: none;
}
/* remove border & off descender */
img {
  border: 0;
  vertical-align: middle;
}
/* remove descender */
svg,
input,
select,
textarea,
label {
  vertical-align: middle;
}
/* set collapse */
table {
  border-collapse: collapse;
}
/* off bold */
th,
em,
strong {
  font-weight: normal;
}
/* all elements to border-box */
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
/* break word for all elements */
*,
*:before,
*:after {
  word-break: break-all;
}
pre {
  white-space: pre-wrap;
}
/* Base style */
html {
  height: 100%;
  margin: 0;
  padding: 0;
}
body {
  font-family: "\30E1\30A4\30EA\30AA", "\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3", "Hiragino Kaku Gothic Pro",
    "\30D2\30E9\30AE\30CE\4E38\30B4\30B7\30C3\30AF   Pro W3", "Hiragino maru Gothic Pro", "MS PGothic",
    "Helvetica", "sans-serif";
  font-size: 13px;
  background: #e3e3e3;
  line-height: 1.5;
  color: #333333;
  word-break: break-all;
  height: 100%;
  margin: 0;
  padding: 0;
}
svg,
input,
select,
textarea,
label {
  font-family: "\30E1\30A4\30EA\30AA", "\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3", "Hiragino Kaku Gothic Pro",
    "\30D2\30E9\30AE\30CE\4E38\30B4\30B7\30C3\30AF   Pro W3", "Hiragino maru Gothic Pro", "MS PGothic",
    "Helvetica", "sans-serif";
}
a {
  color: #324fe1;
  text-decoration: none;
}
a:active {
  text-decoration: underline;
  color: #3399ff;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  text-indent: 0.01px; /*Firefox用*/
  text-overflow: ""; /*Firefox用*/
}
/* cording */
.navMenu {
  max-width: 800px;
  margin: 0 auto;
  overflow-y: scroll;
}
.navMenu ul {
  width: 1691px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 20px;
}
.navMenu li {
  margin-left: 22px;
  position: relative;
}
.navMenu li:first-child {
  margin-left: 0;
}
.navMenu li a {
  font-size: 16px;
  color: #000;
}
.navMenu li .router-link-active {
  color: #bb1d1d;
}
.navMenu li a:hover {
  opacity: 0.6;
}
.wrapper {
  padding: 40px 14px 0;
}
.wrapper h1 {
  text-align: center;
  margin-bottom: 24px;
}
.wrapper h1 span {
  display: inline-block;
}
.wrapper h2 {
  text-align: center;
  font-size: 14px;
  margin-bottom: 4px;
}
/* 問題 */
.quizContent {
  background: #fff;
  padding: 20px 28px;
  margin: 10px auto 0;
  border-radius: 20px;
  max-width: 800px;
}
.questionArea h3 {
  font-size: 16px;
}
.questionArea h4 {
  font-size: 14px;
  margin: 12px 0 10px;
}
.answerList li {
  font-size: 12px;
  background: #ddd;
  padding: 6px 10px;
  margin-top: 12px;
  border-radius: 10px;
}
.answerList li:hover {
  opacity: 0.6;
}
.answerList li:first-child {
  margin-top: 0;
}
/* 結果 */
.resultArea h3 {
  font-size: 16px;
  border-bottom: solid 2px #ddd;
}
.resultArea h4 {
  margin-bottom: 8px;
}
.resultArea section {
  margin-top: 26px;
}
.correct {
  color: #fff;
  background: #a82424;
  font-weight: bold;
  font-size: 14px;
  padding: 10px 20px;
  margin-top: 8px;
  border-radius: 10px;
}
.incorrect {
  color: #000;
  background: #ccc;
  font-weight: bold;
  font-size: 14px;
  padding: 10px 20px;
  margin-top: 8px;
  border-radius: 10px;
}



























































































































































































































































































































































































































































































































































































